import { useUserIds } from "../hooks/users";
import { UserEntry } from "./UserEntry";
import s from "../style/user.module.sass";
import { ProfileForm } from "./ProfileForm";

export function UserList() {
    var users = useUserIds();
    var usrObjs = users.map((usr) => {
        return (<UserEntry key={usr.id} usr={usr}/>);
    });

    return <div className={s.userList}>
        {usrObjs}
    </div>
}